.page {
    background-color: #222;
}

@media only screen and (max-width: 799px) {
    .dropdown {
        display: none;
    }
}

.parent {
    position: relative;
    display: inline-block;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.parent .popup {
    visibility: hidden;
    width: 160px;
    background-color: #555;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 8px 0;
    position: absolute;
    z-index: 1;
    bottom: -75%;
    left: 50%;
    margin-left: -80px;
}

.parent .show {
    visibility: visible;
    -webkit-animation: fadeIn 1s;
    animation: fadeIn 1s;
}

.colors {
    background: #464545;
    font-size: 18px;
    margin-top: 12rem;
    display: inline-block;
    padding: 1px;
}

.NoColors {
    background: #464545;
    font-size: 18px;
    margin-top: 2rem;
    display: inline-block;
    padding: 1px;
}

@-webkit-keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

.imgSrcs {
    width: 50%;
    height: auto;
}

.thStyles {
    color: #fff;
}

.infoBox1 {
    background: white;
    border-radius: 12px;
}

.content {
    padding: 60px 90px;
    font-size: 20px;
    font-weight: 5200;
    text-align: left;
}

.headTxt {
    text-align: left;
    color: rgb(75, 68, 68);
    font-size: 30px;
}

@media(min-width: 1641px) {
    .words {
        display: inline-block;
        width: 50%;
    }
    .imgs img {
        display: inline-block;
        width: 70%;
    }
}

@media(max-width: 1640px) {
    .words {
        display: inline-block;
        width: 80%;
    }
    .imgs img {
        display: inline-block;
        width: 80%;
    }
}

.allCont {
    margin-left: 0px!important;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    background: #222;
    background: linear-gradient(to bottom right, #222 50%, red 50%);
    color: #aaa;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
}

.mainDiv {
    margin-right: 40px;
    margin-left: 40px;
}

.message {
    text-align: center;
    font-weight: 200;
    margin: 0.4em 0;
    font-size: 3.5em;
}

